ul, ol {
	margin: 0 0 1.5em 0;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

.widgetlist {
  a {
    text-decoration: none;
    > span {
      text-decoration: underline;
    }
  }
  img {
    vertical-align: middle;
  }
}